import React from 'react';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';


const HeaderLight = () => {
    return (
        <Navbar expand="lg" className="navbar-custom bg-opacity-100 position-fixed w-100" style={{ top: 0 }} sticky='top'>
            <Container>
                <Navbar.Toggle aria-controls="basic-navbar-nav"/>
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="ms-auto">
                        <Nav.Link href="" className="nav-link-spacing-left-items ">Makalelerimiz</Nav.Link>
                        <Nav.Link href="#home"className="nav-link-spacing-left-items">Ekibimiz</Nav.Link>
                        <Nav.Link href="#home" className="nav-link-spacing-left-items">Hakkımızda</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
                <Navbar.Brand className="me-auto" href="#home">Anka Veteriner</Navbar.Brand>
                <Navbar.Collapse>
                    <Nav className="me-auto">
                        <NavDropdown title="Hizmetlerimiz" id="basic-nav-dropdown" className="nav-link-spacing-right-items">
                            <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
                            <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link href="" className="nav-link-spacing-right-items">Bize Ulaşın</Nav.Link>
                        <Nav.Link href="" className="nav-link-spacing-right-items">Galeri</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
};

export default HeaderLight;