import React from 'react';
import UilClinicMedical from "@iconscout/react-unicons/icons/uil-clinic-medical";
import UilArrowRight from "@iconscout/react-unicons/icons/uil-arrow-right";
import UilMedkit from "@iconscout/react-unicons/icons/uil-medkit";
import UilClock from "@iconscout/react-unicons/icons/uil-clock";

const Information = () => {
    return (
        <div className="grid md:grid-cols-3 grid-cols-1 gap-[24px]">
            <div className="">
                <div
                    className="w-16 h-16 bg-red-600/5 text-red-600 rounded-lg text-2xl flex align-middle justify-center items-center shadow-sm dark:shadow-gray-800">
                    <UilClinicMedical/>
                </div>
                <div className="content mt-6">
                    <h5 className="text-lg font-medium">Bize Ulaşın</h5>
                    <p className="text-slate-400 mt-3">
                        Evcil hayvanınızın aniden ortaya çıkan sağlık sorunlarıyla karşılaştığınızda bize ulaşabilirsiniz.
                    </p>
                </div>
            </div>
            {/*end feature content*/}
            <div className="">
                <div
                    className="w-16 h-16 bg-red-600/5 text-red-600 rounded-lg text-2xl flex align-middle justify-center items-center shadow-sm dark:shadow-gray-800">
                    <UilMedkit/>
                </div>
                <div className="content mt-6">
                    <h5 className="text-lg font-medium">Doktorların Çalışma Saatleri</h5>
                    <p className="text-slate-400 mt-3 mb-1">
                        Veteriner hekimlerimizin çalışma saatleri güncel randevu takvimine göre belirlenmektedir.
                        Randevu almak için lütfen bizimle iletişime geçin.
                    </p>
                </div>
            </div>
            {/*end feature content*/}
            <div className="">
                <div
                    className="w-16 h-16 bg-red-600/5 text-red-600 rounded-lg text-2xl flex align-middle justify-center items-center shadow-sm dark:shadow-gray-800">
                    <UilClock/>
                    <i className="uil uil-clock"/>
                </div>
                <div className="content mt-6">
                    <h5 className="text-lg font-medium">Çalışma Saatleri</h5>
                    <ul className="list-none mt-3">
                        <li className="flex justify-between">
                            <p className="text-slate-400">Pazartesi - Cuma</p>
                            <p className="text-rose-600">9.00 - 19.00</p>
                        </li>
                        <li className="flex justify-between">
                            <p className="text-slate-400">Cumartesi</p>
                            <p className="text-rose-600">10.00 - 19.00</p>
                        </li>
                        <li className="flex justify-between">
                            <p className="text-slate-400">Pazar</p>
                            <p className="text-rose-600">10.00 - 19.00</p>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
)
    ;
};

export default Information;