import React from 'react';
import UilArrowRight from "@iconscout/react-unicons/icons/uil-arrow-right";

function Hero() {
    return (
        <div className="container relative bg-transparent">
            <div className="grid grid-cols-1 mt-0 bg-transparent">
                <img src="../assets/images/logo-icon-40.png" className="bg-transparent" alt=""/>
                <h1 className="font-bold text-white lg:leading-normal leading-normal bg-transparent text-4xl lg:text-5xl mb-3 mt-1">
                    Anka Veteriner Kliniği
                </h1>
                <p className="text-white/60 text-lg max-w-xl bg-transparent">
                    Evcil dostlarımızın sağlığı ve mutluluğu bizim önceliğimizdir. <br/>Anka
                    Veteriner Kliniği, uzman kadrosuyla, modern tıbbi tekniklerle donatılmış
                    bir ortamda, evcil dostlarımızın ihtiyaçlarına özenle ve sevgiyle
                    yaklaşır. Huzurlu bir ortamda, dostlarımızın sağlığı için buradayız.
                </p>
                <div className="mt-8 bg-transparent">
                    <a
                        href=""
                        className="btn bg-rose-700 hover:bg-rose-800 border-rose-700 hover:border-rose-800 text-white rounded-md"
                    >
                        Veteriner Hekiminize Danışın
                    </a>
                </div>
            </div>
        </div>
    );
}

export default Hero;