import React from 'react'

const TeamCard = ({ lg, md, name, job, photo = '01.jpg' }) => {
	const className = `lg:col-span-6  md:col-span-12`
	const imageUrl = require(`../../assets/images/client/${photo}`)

	return (
		<div className={className}>
			<div className="team p-6 rounded-md border border-gray-100 dark:border-gray-700 group bg-white dark:bg-slate-900 text-center row justify-content-center align-items-center">
				<div className="col-auto">
					<img
						src={imageUrl}
						className="h-24 w-24 rounded-full shadow-md dark:shadow-gray-800 "
						alt={name}
					/>
				</div>

				<div className="content mt-4">
					<h3 className="text-lg font-medium hover:text-rose-700 block">{name}</h3>
					<span className="text-slate-400 block">{job}</span>
				</div>
			</div>
		</div>
	)
}

export default TeamCard
